import request from "../../untils/request.js";
import indexData from "../../utils/datas/index.json";
export default {
	data() {
		return {
			title: 'Hello'
		}
	},
	async mounted(){
		const result = await request("http://localhost:3002/getIndexData");
		// 引入json数据，如果发请求拿不到数据，就可以用备用的
		if(!result){
			result = indexData;
		}
		console.log(result); // 测试是否拿到数据
	}

}
